@import "../../global.scss";

.topbar {
    width: 100%;
    height: 70px;
    background-color: #ffffff;
    color: $mainColor;
    position: fixed;
    top: 0;
    z-index: 3;
    transition: all 1s ease;

    .wrapper {
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .left {
            display: flex;
            align-items: center;

            .logo {
                font-size: 40px;
                font-weight: 700;
                text-decoration: none;
                color: #01303f;
                margin-right: 40px;
                transition: all 1s ease;
            }

            .itemContainer {
                display: flex;
                align-items: center;
                margin-left: 30px;

                .icon {
                    font-size: 18px;
                    margin-right: 5px;
                }

                span {
                    font-size: 15px;
                    font-weight: 400;
                }
            }
        }

        .right {
            .hamburger {
                width: 32px;
                height: 25px;
                display: flex; 
                flex-direction: column;
                justify-content: space-between;
                cursor: pointer;

                span {
                    width: 100%;
                    height: 3px;
                    background-color: $mainColor;
                    transform-origin: left;
                    transition: all 2s ease;
                }
            }
        }
    }

    &.active {
        background-color: $mainColor;
        color: #ffffff;

        .wrapper .left .logo {
            color: #ffffff;
        }

        .hamburger {
            span {
                &:first-child {
                    border-color: #ffffff;
                    transform: rotate(45deg);
                    background-color: #ffffff;
                }
                &:nth-child(2) {
                    opacity: 0;
                }
                &:last-child {
                    border-color: #ffffff;
                    transform: rotate(-45deg);
                    background-color: #ffffff;
                }
            }
        }
    }
}

.logo:after {
    content: "}";
}

.logo:before {
    content: "{";
} 